import React from "react";
import geoIcon from "../../assets/Geo.svg";
import "./style.scss";
import { Link } from "react-router-dom";
import { MAIN_PAGE } from "../../routes/route";

export const Header = () => {
   return (
      <header className="header">
        <Link className="header__site-name" to={MAIN_PAGE}>
          Need for drive
        </Link>
        <span className="header__geolocation">
          <img
            className="header__geolocation__icon"
            src={geoIcon}
            alt="GeoLocation"
          />
          <p>Ульяновск</p>
        </span>
      </header>
    );
  }
